import React, { MouseEvent, useEffect, useState } from 'react';
import { createPortal } from 'react-dom';
import style from './Loading.module.css';
import { classnames } from '@/utils';

interface LoadingProps extends Props {
  /**  body  */
  appendBody?: boolean;
  /**
   * ,  ms,  120 ms; , ,  loading ,
   * 
   * */
  delay?: number;
}

const generateClass = classnames(style);

const stopPropagation = (e: MouseEvent) => e.stopPropagation();

let timer: number | null = null;

/**
 * @description loading 
 */
export default function Loading(props: Readonly<LoadingProps>) {
  const { appendBody, delay, ...nativeProps } = props;

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    timer = window.setTimeout(() => {
      setLoading(true);
    }, delay || 120);

    return () => {
      timer && window.clearTimeout(timer);
      return;
    };
  }, []);

  const element = (
    <div
      className={generateClass({
        fixed: !!appendBody,
        'loading-wrapper': true
      })}
      onClick={stopPropagation}
      {...nativeProps}
    >
      <div className={style.loading} onClick={stopPropagation}></div>
    </div>
  );

  return loading ? (
    appendBody ? (
      createPortal(element, document.body)
    ) : (
      element
    )
  ) : (
    <></>
  );
}
