import { isNumber } from '@/utils';

/** base64  */
const CHARACTER_SET =
  'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';

/**
 * @description 
 * @param str 
 * @returns 
 */
const toBytes = (str: string) => new TextEncoder().encode(str);

/**
 * @description  base64 
 * @param str base64 
 * @returns 
 */
const toCodeAt = (str: string) => {
  const codeAt = [];

  for (let i = 0; i < str.length; i++) {
    const index = CHARACTER_SET.indexOf(str[i]);

    index >= 0 && (codeAt[i] = index);
  }

  return codeAt;
};

/**
 *
 * @param str  base64 
 * @returns base64 
 */
const encode = (str: string) => {
  const bytes = toBytes(str);

  let i = 0,
    output = '';

  while (i < bytes.length) {
    const a = bytes[i++],
      b = bytes[i++] || 0,
      c = bytes[i++] || 0;

    // 3  8   24 
    const total = (a << 16) + (b << 8) + c;

    output += CHARACTER_SET.charAt(
      /*  18  6  */ (total >>> 18) & 63
    );
    output += CHARACTER_SET.charAt(
      /*  12  6  */ (total >>> 12) & 63
    );
    output +=
      b !== 0
        ? CHARACTER_SET.charAt(/*  6  6  */ (total >>> 6) & 63)
        : '=';
    output += c !== 0 ? CHARACTER_SET.charAt(/*  6  */ total & 63) : '=';
  }

  return output;
};

/**
 *
 * @description  base64 
 * @param str  base64 
 * @returns 
 */
const decode = (str: string) => {
  const codeAt = toCodeAt(str);

  let i = 0;

  const original = [];

  while (i < codeAt.length) {
    const a = codeAt[i++],
      b = codeAt[i++],
      c = codeAt[i++],
      d = codeAt[i++];

    const n_a = a,
      n_b = b,
      n_c = c || 0,
      n_d = d || 0;

    const total = (n_a << 18) + (n_b << 12) + (n_c << 6) + n_d;

    original.push((total >> 16) & 255);
    isNumber(c) && original.push((total >> 8) & 255);
    isNumber(d) && original.push(total & 255);
  }

  return window.decodeURIComponent(
    new TextDecoder().decode(new Uint8Array(original))
  );
};

export default { encode, decode };
